function getRandomNumber(low, high) {
  var r = Math.floor(Math.random() * (high - low + 1)) + low;
  return r;
}

function getRandomColor() {
  var red_range = [10, 250];
  var green_range = [10, 250];
  var blue_range = [10, 250];
  var a_range = [0.2, 1];

  var red = getRandomNumber(red_range[0], red_range[1]);
  var green = getRandomNumber(green_range[0], green_range[1]);
  var blue = getRandomNumber(blue_range[0], blue_range[1]);

  var alpha = getRandomNumber(a_range[0] * 100, a_range[1] * 100) / 100;

  return {
    r: red,
    g: green,
    b: blue,
    a: alpha,
    rgbaValue: 'rgba(' + red + ',' + green + ',' + blue + ',' + alpha + ')'
  }
}

const window = Dimensions.get("window");
const screen = Dimensions.get("screen");

function WeightsToGraphData(Legends, TempGraph_DataLength, Pens, PenWeighings, StartDate) {
  var DataSets = []
  var EndDate = 0;

  Legends.forEach(Pen => {
    if (PenWeighings[Pen]) {
      var Weighings = PenWeighings[Pen];
      if (Object.keys(Weighings).length >= 1) {
        var DataSet = []
        var DataSetX = []

        Object.keys(Weighings).sort(function(a, b) {
          return Weighings[a].time - Weighings[b].time;
        }).forEach(key => {
          var data = Weighings[key]
          if (data.weight && data.time >= StartDate) {
            var weight = data.weight
            if (Pens && Pens[Pen] && Pens[Pen].datas && Pens[Pen].datas.count){
              weight = weight / Pens[Pen].datas.count
            }
            DataSet.push(weight)
            DataSetX.push(data.time)
            if (data.time > EndDate){
              EndDate = data.time
            }
          }
        });

        while(DataSet.length < TempGraph_DataLength) {
          DataSet.push(null)
          DataSetX.push(null)
        }

        var color = getRandomColor();
        if (DataSet.some(function(i) { return i !== null; })) {
          DataSets.push({Pen: Pen, data: DataSet, dataX: DataSetX, color:(opacity = 1) => color.rgbaValue})
        }
      }
    }
  });
  return {DataSets, EndDate};
}

class DataDisplay extends Component {
  constructor(props){
    super(props);

    const {Periode, Labels, TempGraph, Legends, DataSets, StartDate, EndDate } = this.setData(props)
    this.state = {
      dimensions: {window, screen},

      Periode: Periode,
      Labels: Labels,

      TempGraph: TempGraph,
      Legends: Legends,
      DataSets: DataSets,

      StartDate: StartDate,
      EndDate: EndDate,
      
      ActiveCurve: null,
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;
    const CurrentData = Props.firebase.currentData;

    var Periode = ["Weeks", 8]
    var Labels = []

    var TempGraph = null
    var Legends = []
    var DataSets = []

    var D = new Date()
    var StartDate = D.getTime()
    var EndDate = new Date(D.setDate(D.getDate()+(7*8))) //Default timespand is 2 months or 8 weeks (will be changed acording to batch)

    if (localDataStore) {
      if (localDataStore.batchs && CurrentData.batchId && 
          localDataStore.graphs && CurrentData.curveId) {
        var TempGraph = localDataStore.graphs[CurrentData.curveId];
        var Batch = localDataStore.batchs[CurrentData.batchId];

        //Find Startdate
        StartDate = Batch.time

        //Find TempGraph data
        if (TempGraph) {
          TempGraph.name = CurrentData.curveId
        } else if (localDataStore.graphs["gcurve"]){
          TempGraph = localDataStore.graphs["gcurve"]
          
          TempGraph.name = "Agrisys_Growth"
        }

        var DataX = []
        //Find Periode and X values
        if (TempGraph.periode) {
          Periode[0] = TempGraph.periode
          Periode[1] = TempGraph.data.length
          var currentTime = new Date(StartDate)
          for (var i = 0; i < Periode[1]; i++) {
            DataX.push(currentTime.getTime())
            if (i < Periode[1] -1) {
              switch(Periode[0]) {
                case "days":
                  currentTime = new Date(currentTime.setDate(currentTime.getDate() +1))
                  break;
                case "Month":
                  currentTime = new Date(currentTime.setMonth(currentTime.getMonth() + 1))
                  break;
                case "Weeks":
                default:
                  currentTime = new Date(currentTime.setDate(currentTime.getDate() + 7))
                  break;
              } 
            }
          }
          EndDate = currentTime.getTime();
          DataX.push(EndDate)
        }

        //Set Lables "TempGraph-Legend"
        DataSets.push({
          data: TempGraph.data,
          dataX: DataX,
          color: (opacity = 0.1) => `rgba(0, 0, 0, ${opacity})`, // optional
        })
      }

      //Find Legends And DataSets
      if (localDataStore.weighings) {
        Legends = Legends.concat(Object.keys(localDataStore.weighings).filter(e => (e !== '__jsogObjectId')).reverse())
        var TempData = DataSets
        var TempEndDate = EndDate
        var {DataSets, EndDate} = WeightsToGraphData(Legends, TempGraph.data.length, localDataStore.pens, localDataStore.weighings, StartDate)
        DataSets = TempData.concat(DataSets)
        if (TempEndDate > EndDate) {
          EndDate = TempEndDate
        }

        Legends = Legends.filter(e => (e !== '__jsogObjectId' && (e === TempGraph.name || DataSets.some(i => i.Pen === e))));
        Legends.unshift(TempGraph.name)
      }
      
      //Find EndDate and Lables
      var {Periode, Labels} = this.setGraphPeriode(Periode, StartDate, EndDate, true)
    }
    return {Periode, Labels, TempGraph, Legends, DataSets, StartDate, EndDate }
  }

  setGraphPeriode(Periode, StartDate = null, EndDate = null, returnVal = false) {
    if (!returnVal) {
      if (!Periode){
        Periode = this.state.Periode;
      }
      StartDate = this.state.StartDate;
      EndDate = this.state.EndDate;
    }

    Periode[1] = 0
    var Labels = []

    var CurrentDate = new Date(StartDate)
    while (CurrentDate.getTime() <= EndDate) {
      Periode[1] = Periode[1] +1;
      switch (Periode[0]){
        case "Days":
          Labels.push(CurrentDate.getDate() + "/" + (CurrentDate.getMonth() + 1))
          CurrentDate.setDate(CurrentDate.getDate() + 1)
          break;
        case "Months":
          Labels.push("Month" + Periode[1])
          CurrentDate.setMonth(CurrentDate.getMonth() + 1)
          break;
        case "Weeks":
        default:
          Labels.push(CurrentDate.getDate() + "/" + (CurrentDate.getMonth() + 1))
          CurrentDate.setDate(CurrentDate.getDate() + 7)
          break;
      }
    }
    Periode[1] = Periode[1] -1;
    
    if (returnVal) {
      return {Periode, Labels}
    }
    else {
      this.setState({
        Periode: Periode,
        Labels: Labels,
      })
    }
  }

  getData() {
    this.props.setFirebaseError(null)

    const localDataStore = this.props.firebase.localDataStore;
    const CurrentData = this.props.firebase.currentData;

    if (CurrentData && CurrentData.batchId && localDataStore.batchs[CurrentData.batchId]){
      const batchData = localDataStore.batchs[CurrentData.batchId]
      
      Object.keys(batchData.pens).forEach(Pen => {
        if (Pen === "__jsogObjectId") return null
        this.props.getPen(Object.keys(localDataStore.userData.farms)[0], Pen)
        this.props.getWeights(Object.keys(localDataStore.userData.farms)[0], Pen)
      })

      var TempGraph = ""
      if (CurrentData.curveId) {
        TempGraph = CurrentData.curveId;
      }
      this.props.getCurve(Object.keys(localDataStore.userData.farms)[0], TempGraph)
    }
  }

  checkData(props) {
    if (!props.firebase || props.firebase.firebaseUser === null) {
      NavigationService.navigate("AuthStack")
    }
    if (!props.firebase.currentData || !props.firebase.currentData.batchId){
      NavigationService.navigate("UserBatchs")
    }
    //Mabye call check for localdataStore get farms..
  }

  onChange = ({ window, screen }) => {
    this.setState({
      dimensions: {window, screen},
    })
  };

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);

      if (this.props.firebase.localDataStore) {
        if (this.props.firebase.localDataStore.graphs && this.props.firebase.localDataStore.weighings) {
          const {Periode, Labels, TempGraph, Legends, DataSets, StartDate, EndDate } = this.setData(this.props)
          if (JSON.stringify(this.state.Periode) !== JSON.stringify(Periode) ||
              JSON.stringify(this.state.Labels)!==JSON.stringify(Labels) ||

              JSON.stringify(this.state.TempGraph)!==JSON.stringify(TempGraph) ||
              JSON.stringify(this.state.Legends)!==JSON.stringify(Legends) ||
              JSON.stringify(this.state.DataSets)!==JSON.stringify(DataSets) ||

              JSON.stringify(this.state.StartDate)!==JSON.stringify(StartDate) ||
              JSON.stringify(this.state.EndDate)!==JSON.stringify(EndDate)) {
            this.setState({
              Periode: Periode,
              Labels: Labels,
        
              TempGraph: TempGraph,
              Legends: Legends,
              DataSets: DataSets,
        
              StartDate: StartDate,
              EndDate: EndDate,
            })
          }
        }
      }
    }
  }

  componentDidMount() {
    this.checkData(this.props);
    this.getData();
    Dimensions.addEventListener("change", this.onChange);
  }

  componentWillUnmount(){
    Dimensions.removeEventListener("change", this.onChange);
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    const localDataStore = this.props.firebase.localDataStore //Farmid

    var wide = this.state.dimensions.window.width>1000?true:false
    
    var height = ((this.state.dimensions.window.height - (70*2)) - (Metrics.medium * (this.state.ActiveCurve?5:4))) - (110)
    if (Platform.OS === 'ios' || Platform.OS === 'android') height += 90
    var width = ((this.state.dimensions.window.width - (Metrics.normal * 2))*2) / (wide?2.05:1)
    
    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          <TitleElement Text={this.props.firebase.currentData.batchId?this.props.firebase.currentData.batchId:"-NON-"}/>
          {/*<View>
            <Text>Start date: {new Date(this.state.StartDate).getDate() + "/" + (new Date(this.state.StartDate).getMonth() +1) + "/" + new Date(this.state.StartDate).getFullYear() }</Text>
          </View>*/}
          <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill, {marginHorizontal: 0, paddingHorizontal:-20}]}>
            <ScrollView horizontal={true} showsHorizontalScrollIndicator={wide?false:true} style={[Helpers.fullSize]}>
              {this.state.TempGraph && this.state.DataSets?(
              <LineChart
                //bezier
                style={[{marginLeft: -35, marginRight:Metrics.normal}]} //??? TODO: check if works on web.. not cut in margin...
                width={width}
                height={height}

                data={{
                  labels: this.state.Labels,
                  xMinMax: [this.state.StartDate, this.state.EndDate, this.state.Periode[1]],
                  legend: this.state.Legends.slice(0, this.state.DataSets.length),
                  datasets: this.state.DataSets,
                }}

                onDataPointClick={(data) => {
                  this.setState({
                    ActiveCurve: data
                  })
                }}

                withShadow={false}
                withVerticalLines={true}
                fromZero={false}
                segments={15} //Horizontal-Lines
                
                verticalLabelRotation={0} //Not Working in browser
                xLabelsOffset={0}

                chartConfig={{
                  backgroundColor: Colors.white,
                  backgroundGradientFrom: Colors.white,  //#FFFFFF
                  backgroundGradientTo: Colors.white,     
                  decimalPlaces: 0, // optional, defaults to 2dp
                  color: (opacity = 1) => `rgba(0, 0, 0, ${opacity})`,  //Lines / Grid
                  labelColor: (opacity = 1) => `rgba(0, 0, 0, ${opacity})`, //Lables
                  style: {
                    borderRadius: 16,
                    height: height,
                  },
                  propsForDots: {
                    r: "5",
                    strokeWidth: "0.5",
                    stroke: Colors.white
                  }
                }}
              />):(<></>)}
            </ScrollView>
          </View>
          {/*<View style={[Helpers.buttonContainer, Helpers.rowCenter]}>
            <Button Text="Days" buttonStyle={{width:100, marginLeft:20}}
              onPress={() => {
                this.setGraphPeriode(["Days", 0])
              } }/>
            <Button Text="Weeks" buttonStyle={{width:100, marginLeft:20}}
              onPress={() => {
                this.setGraphPeriode(["Weeks", 0])
              } }/>
            <Button Text="Months" buttonStyle={{width:100, marginLeft:20}}
              onPress={() => {
                this.setGraphPeriode(["Months", 0])
              } }/>
            </View>*/}
          <CurveEditor control={this} props={this.props} state={this.state} />
        </View>
        {IsLoading &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const CurveEditor = (props) => {
  if (Platform.OS === 'ios' || Platform.OS === 'android') {
    if (props.state.ActiveCurve){
      var TimeText = props.state.Labels[props.state.ActiveCurve.index]
      if (props.state.ActiveCurve.dataset.dataX) {
        var Time = new Date(props.state.ActiveCurve.dataset.dataX[props.state.ActiveCurve.index])
        TimeText = Time.getFullYear() + "." + (Time.getMonth() +1) + "." + Time.getDate()
      }

      var Data = props.state.ActiveCurve.value
      return (
        <View style={[Helpers.buttonContainer, Helpers.fullWidth, Helpers.rowCenter, {maxHeight:100}] }>
          <View style={[Helpers.colCenter, Helpers.borderContainer, {borderRadius:0}]}>
            <Text>{TimeText}</Text>
          </View>
          <View style={[Helpers.colCenter, Helpers.borderContainer, {borderRadius:0}]}>
            <Text>{Data} Kg.</Text>
          </View>
        </View>
      )
    }
    else return(<></>)
  } else {
    const [Point, setPoint] = React.useState(0)
    if (!props.state.TempGraph) {
      return(<></>)
    }
    else {
      return (
        <View style={[Helpers.buttonContainer, Helpers.fullWidth, Helpers.rowCenter, {maxHeight:100,marginTop:10}] }>
          <View style={{padding:6, paddingRight: 15}}>
            <Icon name="md-arrow-back" type='ionicon' size={25} color="black" onPress={() => {
              var index = Point;
              index = index -1
              if (index < 0) {
                index = props.state.TempGraph.data.length -1
              }
              setPoint(index)
            }}/>
          </View>
          <Input
              label={props.state.Labels[Point]}
              containerStyle={[{width:"auto", borderWidth:1}]}
              style={[ApplicationStyles.input, Helpers.borderContainer, {borderRadius:0}]}
              onChangeText={text => {
                if (!isNaN(text)){
                  var NewData = props.state.TempGraph.data;
                  NewData[Point] = text
                  props.control.setState({
                    TempGraph: {...props.state.TempGraph, data: NewData}
                  })
                }
              }}
              value={
                props.state.TempGraph.data[Point]
              }
            />
          <View style={{padding:6, paddingRight: 15}}>
            <Icon name="md-arrow-forward" type='ionicon' size={25} color="black" onPress={() => {
              var index = Point;
              index = index +1
              if (index > props.state.TempGraph.data.length -1){
                index = 0
              }
              setPoint(index)
            }}/>
          </View>
          <Input
              label="Curve Name"
              containerStyle={[{width:"auto", borderWidth:1}]}
              style={[ApplicationStyles.input, Helpers.borderContainer, {borderRadius:0}]}
              
              onChangeText={text => {
                var name = text;
                name = name.replace(' ', '_')
                if (isNaN(name[0])){
                  var NewLegends = props.state.Legends;
                  NewLegends[0] = name
                  props.control.setState({
                    TempGraph: {...props.state.TempGraph, name: name},
                    Legends: NewLegends
                  })
                }
              }}
              value={
                props.state.TempGraph.name
              }
            />
        </View>
      )
    }
  }
} 

